import { LOGIN } from '../../constants/urls';
import { nodeApi } from '../index';

const { INITIAL_CALL } = LOGIN;

export const accessPlatformAPI = nodeApi.injectEndpoints({
    endpoints: builder => ({
        getInitialCall: builder.mutation<any, any>({
            query: body => {
                console.log(
                    'Initial Call Login Body----------------> ',
                    body.mobileNumber,
                );
                return {
                    url: INITIAL_CALL,
                    method: 'POST',
                    body: {
                        getaccountholderinforequest: {
                            identity: '233535105508',
                            jwtrequired: 'true',
                        },
                    },
                };
            },
            transformResponse: async responseData => {
                console.log('responseData Initial', responseData);
                return responseData;
            },
            transformErrorResponse: (errorResponse: any) => {
                console.log('errorResponse', errorResponse);
                return errorResponse.data;
            },
        }),
    }),
    overrideExisting: true,
});

export const { useGetInitialCallMutation } = accessPlatformAPI;
