import { GET_DATA } from '../../constants/urls';
import { nodeApi } from '../index';

const { PAYMENT } = GET_DATA;


export const accessplatformApi = nodeApi.injectEndpoints({
    endpoints: builder => ({
        getPaymentData: builder.mutation<any, any>({
            query: body => {
                console.log('PrePayment Body----------------> ', body);
                return {
                    url: PAYMENT,
                    method: 'POST',
                    headers: {
                        'id': '233244304915',
                        'user-agent': 'mobile',
                        // 'Content-Type': 'application/json',
                        // 'Cookie': body?.Cookie,
                        // 'Authorization': body?.token
                    },
                    body: {
                        "billType": body?.billType,
                        "accountNumber": body?.accountNumber,
                        "paymentServiceName": body?.paymentServiceName,
                        "currency": body?.currency,
                        "amount": body?.amount
                    }
                };
            },
            transformResponse: async responseData => {
                console.log('responseData Payment', responseData);
                // const de = await decrypt(responseData);
                return responseData;
            },
            transformErrorResponse: (errorResponse: any) => {
                console.log('errorResponse Payment', errorResponse);
                // return decrypt(errorResponse.data);
                return errorResponse.data;
            },
        }),
    }),
    overrideExisting: true,
});

export const { useGetPaymentDataMutation } = accessplatformApi;
