import { LOGIN } from '../../constants/urls';
import { nodeApi } from '../index';

const { GET_SESSION_ID } = LOGIN;

export const accessPlatformAPI = nodeApi.injectEndpoints({
  endpoints: builder => ({
    getSessionId: builder.mutation<any, any>({
      query: body => {
        console.log('SESSION Verify Body ----------------> ', body);
        return {
          url: GET_SESSION_ID,
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            Cookie: '',
          },
          body: {
            loginrequest: {
              identity: '233244304915',
              credential: {
                secret: '2024',
                type: 'pincode',
              },
            },
          },
        };
      },
      transformResponse: async responseData => {
        console.log('responseData SESSION', responseData);
        return responseData;
      },
      transformErrorResponse: (errorResponse: any) => {
        console.log('errorResponse', errorResponse);
        return errorResponse.data;
      },
    }),
  }),
  overrideExisting: true,
});

export const { useGetSessionIdMutation } = accessPlatformAPI;
